<template>
  <div>
    <section-container class="register-form" header-text="Register">
      <form @submit.prevent>
        <div v-show="error" class="form-message form-error-message">{{ error }}</div>

        <div class="input-group">
          <label for="email">Email</label>
          <input v-model="$v.user.email.$model" id="email" type="text" class="input-base" placeholder="example@gmail.com">
        </div>

        <div class="input-group">
          <label for="fullName">Full name</label>
          <input v-model="$v.user.fullName.$model" id="fullName" type="text" class="input-base" placeholder="Tony Stark">
        </div>

        <div class="input-group">
          <label for="password">Password</label>
          <input v-model="$v.user.password.$model" id="password" :type="passwordInput.type" class="input-base">

          <span @click="togglePasswordVisibility(passwordInput)" class="show-password-icon" :class="{ 'hide-password': passwordInput.isShow }"></span>
        </div>

        <div class="input-group">
          <label for="confirmPassword">Password again</label>
          <input v-model="$v.confirmPassword.$model" id="confirmPassword" :type="confirmPasswordInput.type" class="input-base">

          <span @click="togglePasswordVisibility(confirmPasswordInput)" class="show-password-icon" :class="{ 'hide-password': confirmPasswordInput.isShow }"></span>
        </div>

        <button :disabled="$v.$invalid" @click="submit" class="btn-primary submit-btn btn-margin-top">Register</button>
      </form>
    </section-container>
    <section-container class="login-link-container">
      <span class="register-or-login-text">I already have an account, <router-link to="/login">LOG IN</router-link></span>
    </section-container>
  </div>
</template>

<script>
import SectionContainer from '@/components/SectionContainer'
import { mapActions } from 'vuex'
import { alphaNum, email, maxLength, minLength, required, sameAs } from 'vuelidate/lib/validators'
import togglePasswordVisibility from '@/mixins/togglePasswordVisibility'

export default {
  name: 'Register',
  components: { SectionContainer },
  mixins: [togglePasswordVisibility],
  data() {
    return {
      error: null,
      user: {
        email: "",
        fullName: "",
        password: ""
      },
      confirmPassword: ''
    }
  },
  validations: {
    user: {
      email: {
        email,
        required,
        maxLength: maxLength(32)
      },
      fullName: {
        required,
        minLength: minLength(4),
        maxLength: maxLength(64)
      },
      password: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(24),
        alphaNum
      }
    },
    confirmPassword: { required, sameAs: sameAs(function () { return this.user.password }) }
  },
  methods: {
    ...mapActions({
      register: "register"
    }),
    submit() {
      if (!this.$v.invalid) {
        const resolve = () => this.$router.push('/')

        this.register({ ...this.user })
          .then(resolve)
          .catch((error) => {
            this.error = error.message;
          })
      }
    }
  }
}
</script>

<style lang="scss" scoped>

@include breakpoint-to(445px) {
  .register-form, .login-link-container {
    margin-left: 10px;
    margin-right: 10px;
  }
}

</style>
